.sl-title {
  letter-spacing: -.04em;
  color: #111;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
}

.sl-part {
  color: #4f2ee8;
}

.sl-list > li {
  margin-bottom: 20px;
}

.trash-btn {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background: #4f2ee8;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  top: 14px;
  right: 14px;
}

.sl-card {
  height: 170px;
  width: 100%;
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  display: flex;
}

.sl-book-img {
  border-radius: 8px;
  margin: 14px;
}

.sl-book-cat {
  letter-spacing: .03em;
  color: #b4afaf;
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.sl-book-title {
  max-width: 170px;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-top: 14px;
  margin-bottom: 4px;
  overflow: hidden;
}

.sl-book-descr {
  height: 68px;
  letter-spacing: -.04em;
  color: #111;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  overflow: scroll;
}

.sl-book-author {
  letter-spacing: -.04em;
  color: #b4afaf;
  margin-right: auto;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.16;
}

.sl-aut-shops, .sl-eshops {
  justify-content: center;
  align-items: center;
  display: flex;
}

.sl-eshops > li:not(:last-child) {
  margin-right: 8px;
}

.sl-message {
  text-align: center;
  margin-bottom: 20px;
}

